import {system} from 'gome-utils-env'
let version = -1;
// const matchResult = navigator.userAgent.match(/gomeplus\/[iphone\/]{0,7}(\d*)|gome\/[iphone\/]{0,7}(\d*)/)
const matchResult = navigator.userAgent.match(/(gome(plus|backup)|GomeSeller)?\/[iphone\/]{0,7}(\d*)\//)
version = matchResult ? matchResult[3] : -1

export default version



