    toast (data, ok, fail) {
        return Promise.resolve(new Promise((resolve, reject) => {
            if(env.app){
                if(!!beforeFuse){
                    if(env.hybrid){
                        navigator.gome.util.nativeUtils.showToast(data);
                    }else{
                        eventbus.emit('toast', data);
                        eventbus.emitDOM('toast', data);
                    }
                }else{
                    GomeJSBridge.toast(data =>{
                        cb(ok,resolve,data)
                    },()=>{
                        cb(fail,reject)
                    },data)
                }
            }else{
                eventbus.emit('toast', data);
                eventbus.emitDOM('toast', data);
            }

            // if(env.hybrid){
            //     navigator.gome.util.nativeUtils.showToast(data);
            // }else{
            //     eventbus.emit('toast', data);
            //     eventbus.emitDOM('toast', data);
            // }
            setTimeout(() => {
                cb(ok, resolve);
            }, 1000)
        }))
    }
